.hero-content {
  position: relative;
  z-index: 2;
  align-items: center;
}

/* Contenitore video */
.video-background {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

/* Video base */
.video-background video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s ease;
}

/* --- Ottimizzazione mobile --- */
@media (max-width: 768px) {
  .video-background video {
    /* leggermente più zoomato per evitare bande nere verticali */
    transform: translate(-50%, -50%) scale(1.15);
    object-position: center center;
  }
}

