/* --- CONTENUTO TESTO --- */
.hero-content {
  position: relative;
  z-index: 2;
  /* sopra il video */
  text-align: center;
  color: white;
  top: calc(50% - 50px);
  transform: translateY(-50%);
  width: 100%;
  padding: 1rem;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}

/* --- RESPONSIVE --- */
@media (max-width: 768px) {
  .hero {
    height: 100vh;
  }

  .video-background video {
    object-fit: cover;
    /* riempie in verticale, taglia i lati */
    width: 100%;
    height: 100%;
  }

  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-content h2 {
    font-size: 1.2rem;
  }
}


.shadow-hover {
  transition: box-shadow 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.shadow-hover:hover {
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card-item {
  flex: 0 1 calc(33.333% - 20px);
  /* 3 per riga, con un po' di margine */
  margin: 10px;
}

@media (max-width: 992px) {
  .card-item {
    flex: 0 1 calc(50% - 20px);
    /* 2 per riga */
  }
}

@media (max-width: 576px) {
  .card-item {
    flex: 0 1 calc(100% - 20px);
    /* 1 per riga */
  }
}

.banner-container {
  position: relative;
  overflow: hidden;
  background-color: black;
  /* fallback */
  color: white;
}

.banner-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.3;
  /* opzionale, per rendere il testo più leggibile */
  z-index: 0;
}

.banner-content {
  position: relative;
  z-index: 1;
}